body{
    margin: 0;
    padding: 0;
}
section{
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

#sudoku-box{
    width: 600px;
    height: 600px;
    text-align: center;
    display: grid;
    grid-template-columns: repeat(9, 1fr);
}

.cell{
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: bold;
    font-size: 1.2rem;
    /* border: solid 1px #ececec; */
    overflow: hidden;
}

.cell input{
    all: unset;
    box-sizing: border-box;
    border: solid 1px #ececec;
    width: 100%;
    height: 100%;
    cursor: pointer;
    caret-color: transparent;
}
.cell input:focus{
    caret-color: transparent;
}

.cell.top input{
    border-top: solid 2px #7a7a7a;
}
.cell.bottom input{
    border-bottom: solid 2px #7a7a7a;
}
.cell.left input{
    border-left: solid 2px #7a7a7a;
}
.cell.right input{
    border-right: solid 2px #7a7a7a;
}

#btn-box{
    margin-top: 30px;
    width: 100%;
    display: flex;
    justify-content: center;
    gap: 20px;
}

#btn-box button{
    all: unset;
    display: flex;
    justify-content: center;
    align-items: center;
    min-width: 100px;
    border-radius: 4px;
    height: 30px;
    padding: 5px;
    background-color: steelblue;
    color: white;
    cursor: pointer;
}

#btn-box button:hover{
    opacity: .8;
}

.number-box{
    display: flex;
    gap: 10px;
}
.number-box .remain-number{
    text-align: center;
}

.number-box .remain-number p{
    display: flex;
    justify-content: center;
    align-items: center;
    background-image: linear-gradient(rgba(249, 95, 213, 0.2), rgba(249, 95, 213, 0.2));
    width: 60px;
    height: 60px;
    font-size: 24px;
    font-weight: bold;
    border-radius: 12px;
}
.number-box .remain-number p.finish{
    background-image: linear-gradient(rgba(249, 95, 213, 0.1), rgba(249, 95, 213, 0.1));
    color: #c0c0c0;
}

@media ( max-width : 768px ) {
    #sudoku-box{
        width: 400px;
        height: 400px;
    }
}